<template>
    <div class="project-details">
        <div class="project-item-wrapper">
		    <div class="project-title">
		        <span class="project-status" :class="project.status" v-if="project.status">{{project.status}}</span><span class="project-name">{{project.projNm ? project.projNm : '---'}}</span>
		    </div>
		    <div class="project-info">
		    	<div class="project-info-row">
		    		<div class="project-info-label">{{$t("Project.Period")}}</div>
		    		<div class="project-info-value" v-if="project.projFrom && project.projTo">{{project.projFrom | dateFormat}} ~ {{project.projTo | dateFormat}}</div>
		    		<div class="project-info-value" v-if="!project.projFrom || !project.projTo">---</div>
		    	</div>
		    	<div class="project-info-row">
		    		<div class="project-info-label">{{$t("Project.OrderingGent")}}</div>
		    		<div class="project-info-value" v-if="project.orderComp">{{project.orderComp}}</div>
		    		<div class="project-info-value" v-if="!project.orderComp">---</div>
		    	</div>
		    	<div class="project-info-row">
		    		<div class="project-info-label">{{$t("Project.BusinessField")}}</div>
		    		<div class="project-info-value">
		    			<span v-if="project.bizField1">{{project.bizField1}}</span>
		    			<span v-if="!project.bizField1">----</span>
		    		</div>
		    	</div>
		    	<div class="project-info-row">
		    		<div class="project-info-label">{{$t("Project.PlaceOfConduct")}}</div>
		    		<div class="project-info-value">
		    			<span v-if="project.location1">{{project.location1 ? project.location1 : ''}} {{project.location2 ? project.location2 : ''}} {{project.location3 ? project.location3 : ''}}</span>
		    			<span v-if="!project.location1">---</span>
		    		</div>
		    	</div>
		    	<div class="project-info-row">
		    		<div class="project-info-label">{{$t("Project.ProjectContent")}}</div>
		    		<div class="project-info-value">
		    			<span v-if="project.projDesc">{{project.projDesc}}</span>
		    			<span v-if="!project.projDesc">---</span>
		    		</div>
		    	</div>
		    	<div class="project-info-row">
		    		<div class="project-info-label">{{$t("Project.ProjectReference")}}</div>
		    		<div class="project-info-value">
		    			<span v-if="project.projFile && project.projRealFile"><a download v-bind:href="project.projRealFile.path">{{project.projFile}}</a></span>
		    			<span v-if="!project.projFile">---</span>
		    		</div>
		    	</div>
		        <div class="tags-list-container" >
		            <span class="info tags" v-if="project.tagsList && project.tagsList.length > 0">
		                <span class="tag" v-for="(tag, index) in project.tagsList" :key="index">{{tag}}</span>
		            </span>
		        </div>
		    </div>
		</div>
    </div>
</template>

<script>
export default {
    name: 'ProjectDetails',
    props: {
        project: {
            type: Object,
            required: true
        }
    },
    components: {
    }
}
</script>

<style lang="scss">
@import '@/assets/styles/projects.scss';
@import '@/assets/styles/project-details.scss';
</style>
