<template>
    <div class="recruiment-item">
        <div class="project-info-row">
    		<div class="project-info-label">{{$t("Project.RecruitmentField")}}</div>
    		<div class="project-info-value" v-if="recruitmentInfo.detailTask">{{ recruitmentInfo.detailTask }}</div>
            <div class="project-info-value" v-if="!recruitmentInfo.detailTask">---</div>
    	</div>
    	<div class="project-info-row">
    		<div class="project-info-label">{{$t("Project.TechnicalClassification")}}</div>
    		<div class="project-info-value">SAP-FI</div>
    	</div>
    	<div class="project-info-row">
    		<div class="project-info-label">{{$t("Project.RecruitingYear")}}</div>
    		<div class="project-info-value" v-if="recruitmentInfo.career">{{recruitmentInfo.career}} {{$t("Project.Years")}}</div>
            <div class="project-info-value" v-if="!recruitmentInfo.career">---</div>
    	</div>
    	<div class="project-info-row">
    		<div class="project-info-label">{{$t("Project.ProgressPreriod")}}</div>
            <div class="project-info-value" v-if="recruitmentInfo.projFrom && recruitmentInfo.projTo">{{recruitmentInfo.projFrom | dateFormat}} ~ {{recruitmentInfo.projTo | dateFormat}}</div>
            <div class="project-info-value" v-if="!recruitmentInfo.projFrom || !recruitmentInfo.projTo">---</div>
    	</div>
    	<div class="project-info-row">
    		<div class="project-info-label">{{$t("Project.RecruitmentPersonal")}}</div>
    		<div class="project-info-value" v-if="recruitmentInfo.rcrtNum">{{ recruitmentInfo.rcrtNum }}</div>
            <div class="project-info-value" v-if="!recruitmentInfo.rcrtNum">---</div>
    	</div>
    	<div class="project-info-row actions">
    		<div class="action-btn-container">
    			<Button :label="$t('Project.Apply')" primary @click.native="apply" v-if="!recruitmentInfo.applyEntity"></Button>
                <Button :label="$t('Project.Applied')" primary :isDisabled="true" v-if="recruitmentInfo.applyEntity"></Button>
    		</div>
    		<div class="action-btn-container">
    			<Button :label="$t('Project.Inquire')" default></Button>
    		</div>
    	</div>
    </div>
</template>

<script>
import moment from 'moment'
import Button from '@/components/Common/Button/Button'
import {
    APPLY_PROJECT
} from '@/mutations/Project.mutation'

export default {
    name: 'RecruimentItem',
    props: {
        recruitmentInfo: {
            type: Object,
            required: true
        }
    },
    components: {
    	Button
    },
    methods: {
        async apply() {
            let userId = this.$store.state.Auth.user.userId

            if (userId) {
                let params = {
                    projCd: this.recruitmentInfo.projCd,
                    userId: userId,
                    seq: this.recruitmentInfo.seq
                }

                let result = await this.$store.dispatch("APPLY_PROJECT_REQUEST", params)
                
                if (result)
                    this.recruitmentInfo.applyEntity = result
            } else {
                // Redirect to login
                return this.$router.push('/auth/login')
            }
        },
        toInquiry() {
            
        }
    }
}
</script>

<style lang="scss" scoped>
@import '@/assets/styles/projects.scss';
@import '@/assets/styles/project-details.scss';
</style>
