<template>
    <div class="mobile-page project-details-container">
        <MainLayout :title="`Project Detail`" :leftIcon="`LEFT_ARROW`" :leftAction="() => { goBack() }">
            <div class="padding-content project-content">
                <ProjectDetails :project="projectInfo"></ProjectDetails>
                <RecruitmentsList :recruiments="projectRecruitments"></RecruitmentsList>
            </div>
        </MainLayout>
    </div>
</template>

<script>
import moment from 'moment'
import MainLayout from '@/components/Common/Layout/MainLayout'
import ProjectDetails from '@/components/Project/Details'
import RecruitmentsList from '@/components/Project/RecruitmentsList'
import { setTimeout } from 'timers'
import {
    FETCH_PROJECT
} from '@/mutations/Project.mutation'

export default {
    name: 'ProjectDetailsContainer',
    components: {
        MainLayout,
        ProjectDetails,
        RecruitmentsList
    },
    data() {
        return {
            isOpen: false,
            projectInfo: {},
            projectRecruitments: [],
            projectData: {},
            recruitmentsList: []
        }
    },
    mounted: async function () {
        this.$nextTick(function () {
            // Get project data by id
            this.getProjectData()
        })
    },
    methods: {
        goBack() {
            this.$router.go(-1)
        },
        async getProjectData() {
            if (this.$route.params.id) {
                try {
                    let projCd = this.$route.params.id
                    let userId = this.$store.state.Auth.user.userId

                    if (projCd && userId) {
                        let params = {
                            projCd: projCd,
                            userId: this.$store.state.Auth.user.userId,
                        }
                        await this.$store.dispatch("FETCH_PROJECT_DETAIL", params)
                        this.projectInfo = this.$store.state.Project.projectDetail.PROJ_INFO
                        this.projectInfo.tagsList = this.projectInfo.keyword ? this.projectInfo.keyword.split(',') : []
                        this.projectRecruitments = this.$store.state.Project.projectDetail.RCRT_LIST
                    } else {
                        return this.$router.push('/auth/login')
                    }
                    
                } catch (error) {
                    console.error(error)
                }

            } else {
                // Redirect to homepage
            }
        }
    }
}
</script>

<style lang="scss">
    @import '@/assets/styles/variables.scss';
    @import '@/assets/styles/splash.scss';

    .project-content {
        background-color: #F5F5F5;
    }
</style>